import React from 'react'
import { Flex, Box, Text } from 'rebass'
import { useStaticQuery, graphql } from 'gatsby';
import Divider from '../../components/Divider';
import styled from '@emotion/styled/macro';

const StyledBox = styled(Box)`
  text-align: center;
`

const Image = styled.img`
  width: 30px;
  height: 30px;
  margin: auto;
  margin-bottom: 1rem;
`

const ProductSidenote = () => {
  const sidenoteQuery = useStaticQuery(graphql`
    query SidenoteStaticQuery {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            sidenotes {
              imageSrc
              reminder
              footnotes
            }
          }
        }
      }
    }
  `);

  const { sidenotes } = sidenoteQuery.site.siteMetadata.gatsbyStorefrontConfig;

  return (
    <Box>
      {sidenotes.map((note, index) => {
        return (
          <StyledBox key={index}>
            <Text>{note.reminder}</Text>

            <Divider bg="grey" my={"1rem"} />

            <Image src={note.imageSrc} alt={note.caption} />
            <Text>{note.footnotes}</Text>
          </StyledBox>
        )
      })}
    </Box>
  )
}

export default ProductSidenote
