import React, { useState } from 'react';
import { Flex, Box, Text } from 'rebass';
import { Helmet } from 'react-helmet';
import loadable from '@loadable/component';
import { CarouselProvider } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import styled from '@emotion/styled';

import strings from './strings.json';
import substrDescription from '../../utils/substrDescription';
import ProductCounter from '../../components/ProductCounter';
import Divider from '../../components/Divider';
import Breadcrumbs from '../../components/Breadcrumbs';

import ProductGalleryThumbnails from './ProductGalleryThumbnails';
import ProductGalleryCarousel from './ProductGalleryCarousel';
import { CurrentVariantContextProvider } from './CurrentVariantContext';
import ProductVariantSelector from './ProductVariantSelector';
import ProductVariantAddToCart from './ProductVariantAddToCart';
import ProductVariantPrice from './ProductVariantPrice';
import ProductVariantSku from './ProductVariantSku';
import ProductDescription from './ProductDescription';
import ProductReviews from './ProductReviews';
import ProductRelated from './ProductRelated';
import ProductPolicy from './ProductPolicy';
import ProductSidenote from './ProductSidenote';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import './style.css'

const ProductTabs = styled(Tabs)`

  & .nav-link {
    padding: 3vw 2vw;
    font-size: inherit;
  }
`

// react-payment-icons-inline heavily increases webpack bundle size. Need to find alternative solution. Will disable it for now.
// const Payments = loadable(() => import('../../components/Payments'));
const ShareButtons = loadable(() => import('../../components/ShareButtons'));
const DescriptionBox = loadable(() => import('./DescriptionBox'));

const {
  productQuantityLabel,
  paymentsLabel,
  shareButtonsLabel,
  vendorLabel,
  productTypeLabel,
  relatedProductsLabel
} = strings;

function ProductPage({ data, pageContext, location }) {
  const [currentAmount, setCurrentAmount] = useState(1);

  const {
    product: {
      id,
      title,
      description,
      images,
      variants,
      options,
      vendor,
      productType,
      cmsConnection,
      reviewsConnection: reviews,
    },
  } = data;

  const cmsData = cmsConnection || {};

  const {
    shortDescription,
    descriptionHtml: withoutShortDescription,
    descriptionSections: sections,
  } = cmsData;

  // There are cases when product doesn't belong to any collection.
  // In this case we need to set a guard in case "collection" and "fields" props undefined.
  const { collection } = data || {};
  const { fields } = collection || {};
  const { title: collectionTitle } = collection || {
    title: null,
  };
  const { shopifyThemePath: collectionPath } = fields || {
    shopifyThemePath: null,
  };

  const { cartUrl } = pageContext;
  const {
    shareButtons,
    productImagesCarouselProps,
    reviewsNumberPerPage,
  } = data.store.siteMetadata.gatsbyStorefrontConfig;

  function increaseAmount() {
    setCurrentAmount((a) => a + 1);
  }

  function decreaseAmount() {
    setCurrentAmount((a) => (a <= 1 ? 1 : a - 1));
  }

  return (
    <>
      <Helmet>
        {/* Google's meta description length is up to 920 pixels, which might
          allow for up to 158 characters. On mobile devices, the max limit is
          about 680 pixels and 120 characters. Oct 1, 2019 */}
        <meta
          name="description"
          content={
            shortDescription
              ? substrDescription(shortDescription, 158)
              : substrDescription(description, 158)
          }
        />
      </Helmet>

      <Box px={["5vw", "3.5vw"]} pt={["25vw", "18vw", "16vw"]} sx={{ background: 'url("//cdn.shopify.com/s/files/1/0117/9005/7534/files/bg-face.jpg")' }}>
        <Box my="1rem">
          <Breadcrumbs
            productTitle={title}
            collectionTitle={collectionTitle}
            collectionPath={collectionPath}
            separator="/"
          />
        </Box>

        <CarouselProvider
          naturalSlideWidth={
            productImagesCarouselProps.naturalSlideWidth
              ? productImagesCarouselProps.naturalSlideWidth
              : images[0]
              ? images[0].localFile.childImageSharp.main.width
              : 1
          }
          naturalSlideHeight={
            productImagesCarouselProps.naturalSlideHeight
              ? productImagesCarouselProps.naturalSlideHeight
              : images[0]
              ? images[0].localFile.childImageSharp.main.height
              : 1
          }
          totalSlides={images.length}
        >
          <Flex
            flexDirection={['column', 'row']}
            bg="background"
            sx={{
              borderWidth: '1px',
              borderStyle: 'solid',
              borderColor: 'grey',
            }}
          >
            <Flex
              width={[1, 3 / 5]}
              py={["5vw", "3vw", "5vw"]}
              pr={images && images.length > 1 ? [2, null, 3] : [2, null, 3]}
              pl={images && images.length > 1 ? [2, null, 3] : [2, null, 0]}
              flexDirection="column"
              data-product-image-container
            >
              <ProductGalleryCarousel
                images={images}
                title={title}
                maxContainerHeight={productImagesCarouselProps.naturalSlideHeight}
              />
              
              {images && images.length > 1 ? (
                <Box
                  width={1}
                  py={2}
                  px={[2, null, 0]}
                >
                  <ProductGalleryThumbnails
                    images={images}
                    title={title}
                    maxContainerHeight={600}
                  />
                </Box>
              ) : (
                ''
              )}
            </Flex>

            <Flex
              width={[1, 2 / 5]}
              p={["5vw", "3vw", "5vw"]}
              flexDirection="column"
              data-product-info
              sx={{ borderLeftWidth: [0, '1px'], borderLeftStyle: 'solid', borderLeftColor: 'grey' }}
            >
              <CurrentVariantContextProvider>
                <Box>
                  <Text mb={3} fontSize={["6vw", "2vw"]} data-title-box>
                    {title}
                  </Text>
                  
                  <ProductVariantPrice
                    initialDisplayPrice={variants[0].price}
                    mb={3}
                  />
                  {shortDescription ? (
                    <DescriptionBox
                      source={shortDescription}
                      escapeHtml={false}
                      mb={3}
                    />
                  ) : (
                    <ProductDescription
                      description={withoutShortDescription || description}
                      sections={sections}
                    />
                  )}
                </Box>

                <Divider bg="grey" my={"2rem"} />

                <ProductVariantSelector
                  variants={variants}
                  options={options}
                  pageContext={pageContext}
                  mb={4}
                />

                <Box display={["block", "flex"]} alignItems="center">
                  <Box width={[1, 1 / 3]}>
                    <ProductCounter
                      decreaseAmount={decreaseAmount}
                      increaseAmount={increaseAmount}
                      currentAmount={currentAmount}
                    />
                  </Box>
                  <Box width={[1, 2 / 3]} height={"100%"} mt={["1rem", 0]} pl={[0, "1rem"]}>
                    <ProductVariantAddToCart
                      amount={currentAmount}
                      cartUrl={cartUrl}
                    />
                  </Box>
                </Box>

                {/*
                <Flex mb={4}>
                  <Box>
                    <Text>{paymentsLabel}</Text>
                    <Payments payments={payments} />
                  </Box>
                </Flex>
                */}

                <Divider bg="grey" my={"2rem"} />
                
                <ProductPolicy />

                <ProductVariantSku />
              </CurrentVariantContextProvider>

              {/*
              {vendor ? (
                <Flex mb={4}>
                  <Box mr={2}>
                    <Text>{vendorLabel}</Text>
                  </Box>
                  <Box>{vendor}</Box>
                </Flex>
              ) : (
                ''
              )}

              {productType ? (
                <Flex mb={4}>
                  <Box mr={2}>
                    <Text>{productTypeLabel}</Text>
                  </Box>
                  <Box>{productType}</Box>
                </Flex>
              ) : (
                ''
              )}

              <Flex mb={4} alignItems="center">
                <Box mr={2}>
                  <Text>{shareButtonsLabel}</Text>
                </Box>
                <Box>
                  <ShareButtons buttons={shareButtons} location={location.href} />
                </Box>
              </Flex>
              */}
            </Flex>
          </Flex>

          {/* Tabs */}
          <Box sx={{ bg: 'transparent' }} mt={5}>
            <ProductTabs defaultActiveKey="description">
              <Tab eventKey="description" title="Description">
                <Box
                  textAlign={["center", "left"]}
                  bg='background'
                  px={["5vw", "3.5vw"]}
                  py={["10vw", "3.5vw"]}
                  mx="auto"
                  width={1}
                >
                  <ProductDescription
                    description={withoutShortDescription || description}
                    sections={sections}
                  />
                </Box>
              </Tab>

              <Tab eventKey="details" title="Product Details">
                <Flex flexDirection={["column", "row"]} bg="background" mx="auto" p={["5vw", "3.5vw"]} width={1}>
                  <Box pr={[0, "3.5vw"]} pt={["5vw", "3.5vw"]} pb={["10vw", "3.5vw"]} width={[1, 2 / 3]} textAlign={["center", "left"]}>
                    <ProductDescription
                      description={withoutShortDescription || description}
                      sections={sections}
                    />
                  </Box>

                  <Box p={["5vw", "3.5vw"]} width={[1, 1 / 3]} bg="backgroundGrey" textAlign={'center'}>
                    <ProductSidenote />
                  </Box>
                </Flex>
              </Tab>
              
              <Tab eventKey="reviews" title="Reviews" disabled>
                <Box bg='background'
                  py={'3rem'}
                  px={'2rem'}
                  mx="auto"
                  width={1}
                >
                  {reviews && reviews.length > 0 ? (
                    <ProductReviews
                      reviews={reviews}
                      reviewsNumber={reviews.length}
                      paginationNum={reviewsNumberPerPage}
                    />
                  ) : (
                    ''
                  )}
                </Box>
              </Tab>
            </ProductTabs>
          </Box>
        </CarouselProvider>
      </Box>

      {/* Related products */}
      <Box bg="background" sx={{ borderTopWidth: '1px', borderTopStyle: 'solid', borderTopColor: 'grey' }}>
        <Text my={"2rem"} fontSize={["6vw", "3vw", "2vw"]} fontWeight="bold" textAlign="center">{relatedProductsLabel}</Text>
        <Box mx={["5vw", "3.5vw"]}>
          <ProductRelated prodType={productType} prodId={id}/>
        </Box>
      </Box>
    </>
  );
}

export default ProductPage;
