import React from 'react'
import { Flex, Box, Text } from 'rebass'
import { useStaticQuery, graphql } from 'gatsby';
import styled from '@emotion/styled/macro';
import { Styled } from 'theme-ui';

const StyledBox = styled(Box)`
  display: inline-grid;
  margin-bottom: 1rem;
  text-align: center;
`

const Image = styled.img`
  width: 70%;
  margin: auto;
`

const ProductPolicy = () => {
  const policyQuery = useStaticQuery(graphql`
    query PolicyStaticQuery {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            policies {
              imageSrc
              caption
            }
          }
        }
      }
    }
  `);

  const { policies } = policyQuery.site.siteMetadata.gatsbyStorefrontConfig;

  return (
    <Box>
      {policies.map((policy, index) => {
        return (
          <StyledBox width={1 / 2} key={index}>
            <Image src={policy.imageSrc} alt={policy.caption} />
            <Text>{policy.caption}</Text>
          </StyledBox>
        )
      })}
    </Box>
  )
}

export default ProductPolicy
