import React from "react"
import { Flex, Box } from "rebass"
import { useStaticQuery, graphql } from "gatsby"
import IndividualProduct from '../catalog/IndividualProductAnimated'

const ProductRelated = (props) => {
  const { prodType, prodId } = props;

  const data = useStaticQuery(graphql`
    {
      relatedProducts: allShopifyProduct(limit: 4) {
        nodes {
          handle
          id
          title
          productType
          tags
          fields {
            shopifyThemePath
            firstImage {
              altText
              originalSrc
            }
          }
          availableForSale
          priceRange {
            minVariantPrice {
              amount
              currencyCode
            }
            maxVariantPrice {
              amount
              currencyCode
            }
          }
          variants {
            availableForSale
            compareAtPrice
            title
            price
            shopifyId
            sku
            weight
            weightUnit
            selectedOptions {
              name
              value
            }
          }
        }
      }
    }
  `)

  const {
    relatedProducts: {
      nodes,
    },
  } = data;

  const related = data.relatedProducts;

  return (
    <Flex flexDirection={['column', 'row']}>
      {related.nodes.map((product, index) => {
        if (product.id !== prodId && product.productType === prodType) {
          return (
            <Box width={[1, 1 / 4]} key={index}>
              <IndividualProduct product={product} />
            </Box>
          )
        } else {
          ""
        }
      })}
    </Flex>
  )
}

export default ProductRelated
